.formulario{
    display: flex;
    justify-content: center;
    margin: 80px 0;
}
.formulario .form{
    max-width: 80%;
    background: #f2f2f2;
    border-radius: 20px;
    padding: 36px 64px;
    box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.08);
}
.formulario .form .titulo{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: 300;
}