.campo-texto{
    margin: 24px 0;
}

.campo-texto label{
    display: block;
    margin: 0 0 8px 0;
    font-size: 24px;
}

.campo-texto input{
    background: #fff;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
    width: 100%;
    border: none;
    font-size: 24px;
    padding: 24px;
    box-sizing: border-box;
}